.App {
  text-align: center;
}

.selected {
  background: chartreuse !important;
}
img {
  width: 600px;
  height: 400px;
}

@media (max-width: 800px) {
  img {
    width: 300px;
    height: 200px;
  }
}
ul {
  list-style-type: none;
}

ul li {
  margin: 5px;
}

body {
  display: flex;
  justify-content: center;
  font-size: 18px;
}

h1 {
  color: white;
  background-color: black;
  margin-bottom: 5px;
  margin-top: 5px;
  padding: 5px;
  font-family: "Limelight", cursive;
}

.container {
  /* background-color:darkgray; */
  
  border: #000 1px solid;
  margin: 20px;
  padding: 20px;
  box-shadow: 8px 8px 24px 0px rgba(66, 68, 90, 1);
}

.options {
  padding: 10px;
  margin: 10px;
  border: 3px solid #000;
  cursor: pointer;
  width: 50%;
  background-color: white;
  color: steelblue;
  border-radius: 5px;
  font-weight: bold;
}

.quiz div {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.quiz div button {
  padding: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  border: 3px solid #000;
  border-radius: 10px;
  font-size: 20px;
}
